.TodoSearch {
  background: #f9fbfc;
  border-radius: 10px;
  border: 2px solid #202329;
  margin: 0 24px;
  height: 44px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  /*font-family: "Montserrat";*/
  font-weight: 200;
  color: #1e1e1f;
  margin: auto;
  max-width: 500px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch:focus {
  outline-color: #61dafa;
}
.SearchConteiner {
  text-align: center;
}
